
.Body{
    
 
    height: 150vh;
  
    display: flex;
   flex-direction: row;
    
   
  }
  
  .Sidebar { 
    flex-grow: 2;
    
    background-color: red;
   
 }
  .Body-Main { 
    background-color: darkblue;
    flex-direction: row;
    flex-grow: 8;
    
 }
 .Body-Main-Top {  display: flex; height:50vh;}
