 .Body-Main-Middle{ flex-grow: 1; display: flex;height:50vh; }
 .Body-Main-Middle-CoverRight{ flex-grow: 3; background-color: lightblue;display: flex; }
 .Body-Main-Middle-CoverMiddle{ flex-grow: 3; background-color:  coral;display: flex; }
