 .Body-Main-Bottom{   display: flex;height:50vh;}
 .Body-Main-Bottom-Content{ flex-grow:2; background-color: lightgoldenrodyellow; display: flex;}
 .Body-Main-Bottom-Cover{ flex-grow: 3; background-color: orange; display: flex;}

